.pagenotfoundsec1 {
  background-image: url("../../images/404/bg.jpg");
  background-position: center;
  background-size: cover;
  height: calc(100vh - 5.188em);
  background-position: top center;
}
.pagenotfoundsec1 p {
  font-family: Merriweather Sans;
  font-weight: 600;
  color: #ffffff;
  font-size: 1.875em;
  line-height: 1.333em;
  letter-spacing: 0.25px;
  pointer-events: none;
  padding-top: 1em;
}
.pagenotfoundsec1 h1 {
  font-size: 26.688em;
  color: #ffffff;
  font-family: Merriweather Sans;
  font-weight: 700;
  line-height: 1;
  padding: 0.199em 0;
  margin: 0;
  letter-spacing: 0.2px;
  pointer-events: none;
  animation: spooky 2s alternate infinite linear;
}
@media (max-width: 1023px) {
  .pagenotfoundsec1 {
    padding: 7.5em 1.6em;
    height: calc(100vh - 18em);
  }
  .pagenotfoundsec1 p {
    font-size: 2em;
    line-height: 1.2em;
    letter-spacing: 0.25px;
    line-height: 1.6em;
  }
  .pagenotfoundsec1 h1 {
    font-size: 15em;
  }
}
@keyframes spooky {
  from {
    transform: translatey(0.15em) scaley(0.95);
  }
  to {
    transform: translatey(-0.15em);
  }
}
