.btn {
  background-color: #333333;
  cursor: pointer;
  text-align: center;
  display: inline-block;
  position: relative;
  text-decoration: none;
  color: white;
  text-transform: capitalize;
  /* background-color: - add your own background-color */
  font-family: Merriweather Sans;
  font-size: 1.25em;
  letter-spacing: 0.25px;
  line-height: 1.556em;
  padding: 1.167em 0;
  width: 11.111em;
  border-radius: 100px;
  overflow: hidden;
  border: 3px solid #3bb859;
}
.fournotbtn {
  width: 16.111em !important;
}
/* effect-1 styles */
.btn.effect-1 {
  transition: all 0.2s linear 0s;
}
.btn.effect-1:before {
  content: url("../../images/arrowright.png");
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 16px;
  top: 2px;
  opacity: 0;
  height: 100%;
  width: 40px;
  transition: all 0.2s linear 0s;
}
.btn.effect-1:hover {
  text-indent: -20px;
  color: white;
  -webkit-box-shadow: 0px 9px 12px 0px rgba(59, 184, 89, 0.3);
  -moz-box-shadow: 0px 9px 12px 0px rgba(59, 184, 89, 0.3);
  box-shadow: 0px 9px 12px 0px rgba(59, 184, 89, 0.3);
}
.btn.effect-1:hover .fa {
  text-indent: 0px;
}
.btn.effect-1:hover:before {
  opacity: 1;
  text-indent: 0px;
}
@media (max-width: 1520px) {
  .btn {
    width: 12.111em;
  }
}
@media (max-width: 1023px) {
  .btn {
    font-size: 1.4em;
    letter-spacing: 0.25px;
    line-height: 1.714em;
    padding: 0.857em 0;
    width: 11.571em;
  }
}
