.sticky-content {
  position: fixed;
  top: 20%;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  z-index: 1;
  cursor: pointer;
}
.sticky-content .rotate {
  writing-mode: vertical-rl;
}
.sticky-content .enquire {
  background-color: #333333;
  cursor: pointer;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.sticky-content .enquire .text {
  color: #ffffff;
  font-size: 1.563em;
  letter-spacing: 0.2px;
  line-height: 1.8em;
  font-family: Merriweather Sans;
  transform: rotate(180deg);
  padding: 1.68em 0.56em 1.68em 0.56em;
}
@media (max-width: 1024px) {
  .sticky-content {
    position: fixed;
    top: 20%;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    z-index: 1;
  }
  .sticky-content .rotate {
    writing-mode: vertical-rl;
  }
  .sticky-content .enquire {
    background-color: #333333;
    cursor: pointer;
  }
  .sticky-content .enquire .text {
    color: #ffffff;
    font-size: 1.563em;
    letter-spacing: 0.2px;
    line-height: 1.8em;
    font-family: Merriweather Sans;
    transform: rotate(180deg);
    padding: 2.68em 0.76em 2.68em 0.76em;
  }
}
@media (max-width: 1023px) {
  .sticky-content {
    top: initial;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    width: 100%;
  }
  .sticky-content .rotate {
    writing-mode: initial;
  }
  .sticky-content .enquire {
    background-color: #0e993d;
    cursor: pointer;
    width: 100%;
    -webkit-box-shadow: 0px -2px 8px 0px rgba(0, 0, 0, 0.16);
    -moz-box-shadow: 0px -2px 8px 0px rgba(0, 0, 0, 0.16);
    box-shadow: 0px -2px 8px 0px rgba(0, 0, 0, 0.16);
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0;
  }
  .sticky-content .enquire .text {
    color: #ffffff;
    font-size: 1.4em;
    letter-spacing: 0.25px;
    line-height: 1.714em;
    font-family: Merriweather Sans;
    text-align: center;
    transform: rotate(0);
    padding: 1em 0 1em 0;
  }
}
